.loginForm__inputContainer {
    background: #fff;
    margin-top: 16px;
}

.loginForm__row {
    padding-left: 15px;
    padding-right: 15px;
    background: url(data:image/png;base64,)
        repeat-x 0 bottom;
    background-size: auto 1px;
    margin-bottom: 1px;
    display: flex;
}

.loginForm__mobileLabel {
    display: inline-block;
    width: 60px;

    border-right: 1px solid #e0e0e0;
    height: 24px;
    line-height: 24px;
    padding: 10px 0 10px 10px;
    font-size: 16px;
    color: #333;
    position: relative;
}

.loginForm__mobileLabel::after {
    width: 6px;
    height: 6px;
    display: inline-block;
    border-right: 2px solid #bab5ad;
    border-bottom: 2px solid #bab5ad;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    margin: 0 0 0 4px;
    position: relative;
    top: -3px;
    margin-left: 8px;
    content: '';
}

.loginForm__input {
    border: none;
    height: 23px;
    flex: 1;
    line-height: 23px;
    padding: 10px 0 10px 10px;
    font-size: 15px;
    outline: 0;
    color: #333;
}

.loginForm__passwordLabel {
    display: inline-block;
    width: 60px;
    height: 24px;
    line-height: 24px;
    padding: 10px 0 10px 10px;
    font-size: 16px;
    color: #333;
    position: relative;
}

.loginForm__btnContainer {
    margin-top: 12px;
    text-align: center;
    padding: 0 15px;
}

.loginForm__btn {
    width: 100%;
    line-height: 40px;
    color: #fff;
    text-decoration: none;
    background: #f63;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    letter-spacing: 6px;
}
